.card {
  padding: 1.25rem;
}

.toggle __button {
  padding: 1.875rem;
  height: 1.25rem;
  width: 0.9375rem;
  border-radius: 50%;
  background-color: var(--dark-bg);
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*:before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-inter);
}

:root {
  --lime-green: hsl(163, 72%, 41%);
  --bright-red: hsl(356, 69%, 56%);
  --facebook: hsl(208, 92%, 53%);
  --twitter: hsl(203, 89%, 53%);
  --instagram: linear gradient hsl(37, 97%, 70%) to hsl(329, 70%, 58%);
  --toggle-bg: linear gradient hsl(210, 78%, 56%) to hsl(146, 68%, 55%);
  --toggle: hsl(230, 22%, 74%);
  --darkbg: hsl(230, 17%, 14%);
  --dark-top-bg: hsl(232, 19%, 15%);
  --dark-card-bg: hsl(228, 28%, 20%);
  --dark-text1: hsl(228, 34%, 66%);
  --dark-text2: hsl(0, 0%, 100%);
  --light-bg: hsl(0, 0%, 100%);
  --light-top-bg: hsl(225, 100%, 98%);
  --light-card-bg: hsl(227, 47%, 96%);
  --light-text1: hsl(228, 12%, 44%);
  --light-text2: hsl(230, 17%, 14%);
}

:root {
  --font-inter: "Inter", sans-serif;
}

h1,
h2,
h3 {
  margin-top: 0px;
}

a,
a:active,
a:visited {
  text-decoration: none;
}